import tab from './modules/tab';
import Modal from './modules/modal';
import ajax from './modules/ajax';
import slider from './modules/slider';
import calc from './modules/calc';
import timerData from './modules/timer/timerSettings';
import timer from './modules/timer/timer';
import "@babel/polyfill";


let modal = new Modal();
let tData = timerData.init();


function descriptionBtn() {
    let info = document.querySelector('.info');
    let btns = document.getElementsByClassName('description-btn');
    info.addEventListener('click', (evt) => {
        let _target = evt.target;
        let descBtn = _target.closest('.description-btn');
        if (!descBtn) {
            return;
        } else {
            console.log(descBtn.closest('.info-tabcontent') );
            modal.modalOpen()
        }
    });
}

document.addEventListener("DOMContentLoaded", function(){
    tab.init();
    modal.init();
    ajax.init();
    slider.init();
    calc.init();
    descriptionBtn();
    timer.init(tData);
});
